.our_project {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  max-width: 1640px;

  margin: 350px auto 0px auto;
}
.our_project__title {
  position: relative;

  font-family: 'Source Code Pro', monospace;
  font-size: 36px;
  line-height: 46.4px;
  letter-spacing: 0.2em;
  font-weight: 500;
  color: #dcdcdc;
  text-transform: uppercase;
}
.our_project__title::after {
  content: '';
  display: inline-block;

  width: 6px;
  height: 6px;

  margin-left: 24px;

  background-color: #98ec65;
}
.our_project__title::before {
  content: '';
  display: inline-block;

  width: 69px;
  height: 1px;

  background-color: #fff;

  position: absolute;
  bottom: -15px;
}
.our_project__list {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  gap: 20px;

  margin-top: 96px;
}
.our_project_item {
  display: flex;
  flex-direction: column;

  cursor: pointer;
}
.our_project__img {
  max-width: 800;
  max-height: 500;
  width: 100%;
  height: 100%;

  overflow: hidden;

  transition: 0.5s;
}
.our_project__wrapper_img {
  max-width: 800;
  max-height: 500;
  width: 100%;
  height: 100%;

  overflow: hidden;

  border-radius: 6px;
}
.our_project__img:hover {
  transform: scale(1.07);
  transition: 0.5s;
}
.our_project__label {
  margin-top: 20px;

  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 23.2px;
  font-weight: 500;
  color: #fff;
}
.our_project__text {
  margin-top: 5px;

  font-family: 'Source Code Pro', monospace;
  font-weight: 300;
  line-height: 23.2px;
  font-size: 18px;
  color: #fff;
}
.our_project__button {
  margin-top: 80px;

  display: flex;
  align-self: center;
  align-items: center;

  width: 282px;
  height: 27px;

  font-family: 'Source Code Pro', monospace;
  font-weight: 500;
  font-size: 21px;
  line-height: 27.07px;
  color: #fff;

  background-color: transparent;

  cursor: pointer;

  transition: 0.5s;
}
.our_project__button:hover {
  color: #98ec65;
}
.our_project__button {
  color: #fff;
}
.our_project__arrow__margin {
  margin-left: 20px;
}
.our_project__arrow__color {
  transition: 0.5s;
}
.our_project__button:hover .our_project__arrow__color {
  fill: #98ec65;
}

@media screen and (max-width: 1755px) {
  .our_project {
    margin-top: 135px;
    margin-left: 24px;
    margin-right: 24px;
  }
  .our_project__title {
    font-size: 24px;
    line-height: 30.94px;
    letter-spacing: 0.2em;
  }
  .our_project__title::after {
    content: '';
    display: inline-block;

    width: 6px;
    height: 6px;

    margin-left: 24px;

    background-color: #98ec65;
  }
  .our_project__title::before {
    content: '';
    display: inline-block;

    width: 69px;
    height: 1px;

    background-color: #fff;

    position: absolute;
    bottom: -15px;
  }
}

@media screen and (max-width: 954px) {
  .our_project__list {
    flex-direction: column;
    gap: 40px;
  }
  .our_project__button {
    margin-top: 55px;
  }
}
