.popup {
  position: fixed;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.5);

  &__content {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    max-width: 837px;
    min-width: 280px;
    width: 100%;

    max-height: 472px;
    min-height: 120px;
    height: 100%;

    margin: 30px;
    padding: 0 20px;
    background-color: #211f26;

    border-radius: 30px;
  }

  &__close {
    position: absolute;
    top: 28px;
    right: 28px;

    background-image: url(../../../assets/image/close.svg);
    background-color: transparent;

    width: 35px;
    height: 35px;
  }

  &__title {
    font-family: 'Source Code Pro';
    font-size: 32px;
    color: #ffffff;
  }

  &__text {
    margin-top: 20px;
    font-family: 'Source Code Pro';
    color: #ffffff;
  }

  &__eyes {
    width: 100%;
    height: 155px;

    margin: 56px 0;

    background-image: url(../../../assets/image/eyes.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__done {
    width: 188px;
    height: 48px;

    font-family: Montserrat;
    font-size: 18px;
    color: #98ec65;

    background-color: transparent;

    border: 1px solid #98ec65;
    border-radius: 50px;

    transition: 0.2s background-color;
    &:hover {
      color: #000000;

      background-color: #98ec65;
    }
  }
}

.popup_disabled {
  display: none;
}
