.price {
  display: flex;
  flex-direction: column;

  width: 100%;
  max-width: 1640px;

  margin-top: 151px;
}
.price__button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.price__btn_left {
  background-color: transparent;
  width: 95px;
  height: 68px;
}
.price__leftButton__arrow {
  transition: 0.5s;
}
.price__btn_left:hover .price__leftButton__arrow {
  transition: 0.5s;

  stroke: #98ec65;
}
.price__leftButton__border {
  transition: 0.5s;
}
.price__btn_left:hover .price__leftButton__border {
  fill: #98ec65;
}

.price__btn_right {
  background-color: transparent;
  width: 95px;
  height: 68px;
}
.price__rightButton__arrow {
  transition: 0.5s;
}
.price__btn_right:hover .price__rightButton__arrow {
  transition: 0.5s;

  stroke: #98ec65;
}
.price__rightButton__border {
  transition: 0.5s;
}
.price__btn_right:hover .price__rightButton__border {
  fill: #98ec65;
}

.price__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 81px;

  gap: 166px;
}
.price__item {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  border: 2px solid #98ec65;
  border-radius: 80px;

  width: 408px;
  height: 555px;
}
.price__text {
  /* margin-top: 60px; */
  display: flex;
  flex-direction: row;
  align-items: center;

  font-family: 'Source Code Pro', monospace;
  font-size: 14px;
  line-height: 18.05px;
  letter-spacing: -0.05em;
  color: #fff;
}
.price_info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.price__text__img {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.price__calendar {
  width: 22px;
  height: 22px;

  margin-right: 5px;
}
.price__title {
  /* margin-top: 46px; */
  font-family: 'PT Mono', monospace;
  font-size: 24px;
  color: #fff;
  line-height: 30.94px;
}
.price__subtitle {
  /* margin-top: 5px; */
  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  color: #fff;
  line-height: 23.2px;
}
.price__desc {
  /* margin-top: 57px; */
  width: 370px;

  text-align: center;

  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 23.2px;
  color: #fff;
}
.price__order {
  /* margin-top: 74px; */
  width: 171px;
  height: 39px;

  background-color: #98ec65;

  border-radius: 4px;

  margin-bottom: 25px;

  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 23.2px;
  color: #000000;

  transition: 0.5s;
}
.price__order:hover {
  border-radius: 24px;
}
.price__more_details {
  /* margin-top: 25px; */
  display: flex;
  flex-direction: row;
  align-items: center;

  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 23px;
  color: #fff;

  transition: 0.5s;
}
.price__more_details:visited:hover {
  color: #98ec65;

  transition: 0.5s;
}
.price__arrow {
  margin-left: 10px;

  position: relative;
  top: 1px;

  fill: #fff;

  transition: 0.5s;
}
.price__more_details:hover .price__arrow {
  transition: 0.5s;

  fill: #98ec65;
}
.price__more_details:visited {
  transition: 0.5s;

  color: #fff;
}

@media screen and (max-width: 1460px) {
  .price__list {
    gap: 70px;
  }
}

@media screen and (max-width: 1270px) {
  .price {
    width: 100%;
    overflow: hidden;
  }
  .price__list {
    gap: 60px;
  }
  .price__item {
    width: 286px;
    height: 394px;
  }
  .price__calendar {
    width: 11px;
    height: 11px;
  }
  .price__text {
    font-size: 8px;
    line-height: 11px;
    letter-spacing: -0.05em;
  }
  .price__subtitle {
    font-size: 14px;
    line-height: 18.05px;
  }
  .price__title {
    font-size: 18px;
    line-height: 23.2px;
  }
  .price__desc {
    width: 262px;
    height: 72px;

    margin: 0px 12px;

    font-size: 14px;
    line-height: 18.05px;
  }
  .price__order {
    width: 120px;
    height: 28px;

    font-size: 14px;
    line-height: 18.05px;
  }
  .price__more_details {
    font-size: 14px;
    line-height: 18.05px;
  }
}
@media screen and (max-width: 768px) {
  .price__button {
    display: none;
  }
}
@media screen and (max-width: 360px) {
  .price {
    width: calc(100% - 15px);
  }
}
