.itention {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  max-width: 1640px;

  padding: 0px 15px;
  margin-top: 350px;

  &__title {
    margin: 0;

    font-family: 'Source Code Pro', monospace;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 36px;
    line-height: 46.4px;
    letter-spacing: 0.2em;
    color: #dcdcdc;

    width: 100%;

    position: relative;

    margin-bottom: 96px;

    &::after {
      content: '';
      display: inline-block;
      width: 9px;
      height: 9px;
      background-color: #98ec65;
      margin-left: 24px;
    }
    &::before {
      content: '';
      display: inline-block;
      width: 69px;
      height: 1px;
      background-color: #fff;
      position: absolute;
      bottom: -15px;
    }
  }

  &__type {
    text-decoration: none;
  }
}

.itention__container {
  display: flex;
  flex-direction: column;
  gap: 56px;

  width: 100%;
  max-width: 1640px;

  background-image: url(../../../../assets/image/itentionBgLeftBottom.svg);
  background-repeat: no-repeat;
  background-position: 50px 450px;
}

.itention__label {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin: 0;

  font-family: 'PT Mono', monospace;
  font-size: 72px;
  line-height: 92.81px;
  font-weight: 400;
  color: #dcdcdc;

  transition: 0.5s;

  cursor: pointer;

  margin-right: 74px;
}

.itention__item:hover .itention__label {
  color: #98ec65;
}

.itention__item {
  display: flex;
  align-items: center;
}

.itention__arrow {
  margin-left: 74px;
  transition: 0.5s;
}

.itention__item:hover .itention__arrow {
  fill: #98ec65;
}

.itention__arrow__width__768 {
  display: none;

  min-height: 10px;
}

.itention__label:hover::after {
  transition: 0.5s;
}
.itention__img {
  margin-left: 74px;
}

.itention__text {
  margin: 0;

  font-family: 'Source Code Pro', monospace;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  color: #797979;
}

.itention__web {
  margin-left: 10%;
}
.itention__online_store {
  margin-left: 20%;
}
.itention__corporate_site {
  margin-left: 30%;
}
.itention__arrow__width__360 {
  display: none;
}
.itention__arrow__width__320 {
  display: none;
}
@media screen and (max-width: 1635px) {
  .itention__web {
    margin-left: 5%;
  }
  .itention__online_store {
    margin-left: 15%;
  }
  .itention__corporate_site {
    margin-left: 25%;
  }
}

@media screen and (max-width: 1528px) {
  .itention__web {
    margin-left: 2.5%;
  }
  .itention__online_store {
    margin-left: 7.5%;
  }
  .itention__corporate_site {
    margin-left: 12.5%;
  }
  .itention__container {
    background-image: none;
  }
}
@media screen and (max-width: 1315px) {
  .itention__web {
    margin-left: 1%;
  }
  .itention__online_store {
    margin-left: 3%;
  }
  .itention__corporate_site {
    margin-left: 6%;
  }
}
@media screen and (max-width: 1225px) {
  .itention__web {
    margin-left: 0%;
  }
  .itention__online_store {
    margin-left: 0%;
  }
  .itention__corporate_site {
    margin-left: 0%;
  }
  .itention {
    width: 100%;
    max-width: 850px;
    height: 100%;

    margin-top: 0px;
    margin-left: 105px;
    margin-right: 105px;
  }
  .itention__container {
    align-items: flex-start;
  }
  .itention__arrow__width {
    width: 160px;
  }
  .itention__title {
    font-size: 24px;
    line-height: 30.94px;
  }
  .itention__title::after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    background-color: #98ec65;
    margin-left: 24px;
  }
  .itention__title::before {
    content: '';
    display: inline-block;
    width: 69px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    bottom: -15px;
  }
  .itention__container {
    background-image: none;
  }
  .itention__label {
    font-size: 48px;
    line-height: 61.87px;
  }
  .itention__text {
    font-size: 16px;
    line-height: 16px;
  }
}
@media screen and (max-width: 813px) {
  .itention {
    width: calc(100% - 25px);
  }
  .itention__arrow__width {
    display: none;
  }
  .itention__arrow__width__768 {
    display: inline-block;
    margin-top: 16px;
    transition: 0.5s;
  }
  .itention__type:hover .itention__arrow {
    fill: #98ec65;
  }
}

@media screen and (max-width: 678px) {
  .itention__label {
    font-size: 24px;
    line-height: 30.94px;

    margin: 0px;
  }
  .itention__text {
    font-size: 16px;
    line-height: 16px;
  }
  .itention__arrow__width__768 {
    display: none;
    width: 100%;
    margin: 16px 0px 0px 0px;
  }
  .itention__arrow__width__360 {
    display: inline-block;
  }
  .itention__lending {
    margin: 0px;
    max-width: 240px;
  }
  .itention__web {
    max-width: 303px;
  }
  .itention__online_store {
    max-width: 368px;
  }
  .itention__corporate_site {
    max-width: 415px;
  }
}

@media screen and (max-width: 510px) {
  .itention__arrow__width__360 {
    display: none;
  }
  .itention__arrow__width__320 {
    display: inline-block;
  }
  .itention__label {
    font-size: 24px;
    line-height: 30.94px;
  }
  .itention__text {
    font-size: 14px;
  }
  .itention__web {
    max-width: 245px;
  }
  .itention__lending {
    max-width: 210px;
  }
  .itention__online_store {
    max-width: 280px;
  }
  .itention__corporate_site {
    max-width: 387px;
  }
  .itention__type:last-child {
    width: 280px;
  }
}
