.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 123px;

  margin-top: 65px;

  &__info {
    font-family: 'Source Code Pro', monospace;
    font-size: 18px;
    line-height: 23.2px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    font-weight: 500;
  }

  &__link {
    color: #ececec;
    text-decoration: none;

    padding-bottom: 3px;

    margin: 0px 100px;

    border-bottom: 2px solid transparent;

    transition: 0.5s;
  }

  &__link-border {
    border-bottom: 2px solid #98ec65;
  }

  &__link:hover {
    transition: 0.5s;

    color: #98ec65;
  }
}

@media screen and (max-width: 1755px) {
  .menu__link {
    margin: 0px 60px;
  }
  .menu {
    margin: 0px;
  }
}

@media screen and (max-width: 926px) {
  .menu__link {
    margin: 0px 30px;
  }
}
@media screen and (max-width: 691px) {
  .menu__link {
    margin: 0px 10px;
  }
}

@media screen and (max-width: 553px) {
  .menu__link {
    margin: 0px 5px;
  }
}
@media screen and (max-width: 493px) {
  .menu__link {
    display: none;
  }
}
