.project {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-image: url(../../../../assets/image/created_siteLeftTop.svg), url(../../../../assets/image/CreateSiteRightBottom.svg);
  background-repeat: no-repeat;
  background-position: 100px 250px, right 100%;

  width: 100%;

  overflow: hidden;

  margin-top: 202px;
}
.project__label {
  font-family: 'Source Code Pro', monospace;
  font-size: 72px;
  line-height: 92.81px;
  font-weight: 300;
  color: #dcdcdc;

  width: 100%;
  max-width: 922px;
  height: 290px;
}
.project__label::after {
  content: '';
  display: inline-block;
  position: relative;

  bottom: -5px;

  width: 5px;
  height: 77px;

  background-color: #98ec65;

  animation-name: animalabel;
  animation-duration: 0.7s;
  animation-iteration-count: infinite;
}

@keyframes animalabel {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.project__block {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  max-width: 2000px;
  max-height: 2000px;

  position: relative;
}

.project__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  max-width: 900px;
  height: 100%;
  max-height: 575px;

  position: absolute;
}
.project__collumn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  position: relative;
}
.project__row {
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: space-around;
  margin: 50px 0px;
  position: relative;

  width: 100%;
}
.svg__header {
  position: inherit;
  top: -3000px;

  animation-name: header;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;

  width: 100%;
  max-width: 902px;
}
@keyframes header {
  30% {
    top: 0;
    z-index: 1;
  }
  70% {
    top: 0;
  }
  100% {
    top: -3000px;
  }
}
.svg__left__block {
  position: inherit;
  left: -3000px;

  animation-name: left;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;

  max-width: 262px;
  width: 100%;
}
@keyframes left {
  40% {
    left: 0px;
  }
  80% {
    left: 0px;
  }
  100% {
    left: -3000px;
  }
}
.svg__right__block {
  position: inherit;
  right: -3000px;

  animation-name: right;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-delay: 0.6s;
  animation-fill-mode: forwards;

  max-width: 284px;
  width: 100%;
}
@keyframes right {
  40% {
    right: 0px;
  }
  75% {
    right: 0px;
  }
  100% {
    right: -3000px;
  }
}
.svg__footer {
  display: flex;
  flex-direction: column;
  align-items: center;

  position: inherit;
  bottom: -3000px;

  animation-name: footer;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-delay: 0.8s;
  animation-fill-mode: forwards;

  width: 100%;
  max-width: 738px;
}
@keyframes footer {
  40% {
    bottom: 0px;
    animation-delay: 4s;
  }
  75% {
    bottom: 0px;
  }
  100% {
    bottom: -3000px;
  }
}

