@media screen and (max-width: 1755px) {
  .project {
    background-image: none;
  }
  .project__label {
    margin: 0px 186px;

    font-size: 48px;
    line-height: 61.87px;

    width: 628px;
    height: 186px;
  }
  .project__label::after {
    height: 47px;
  }

  .project__block {
    height: 820px;
  }
  .project__wrapper {
    margin-left: 166px;
    margin-right: 166px;
  }
}

@media screen and (max-width: 618px) {
  .project__label {
    width: calc(100% - 26px);
    height: 150px;

    font-size: 34px;
    line-height: 45.94px;
  }
  .project__label::after {
    width: 3px;
    height: 32px;
  }
}
@media screen and (max-width: 458px) {
  .project__label {
    width: calc(100% - 26px);
    height: 100px;

    font-size: 29px;
    line-height: 30.94px;
  }
  .project__label::after {
    width: 3px;
    height: 27px;
  }
}

@media screen and (max-width: 393px) {
  .project__label {
    height: 110px;
    width: calc(100% - 26px);

    font-size: 26px;
  }
}

@media screen and (max-width: 356px) {
  .project__label {
    width: calc(100% - 26px);

    font-size: 24px;
  }
}

@media screen and (max-width: 331px) {
  .project__label {
    width: calc(100% - 26px);

    font-size: 22px;
  }

  .project__label::after {
    height: 25px;
  }
}
